}
.stock {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  gap: 20px; /* Adjusted gap for better spacing */
  padding: 20px; /* Add padding around the container */
  background-color: #f5f5f5; /* Light background color for contrast */
}

.stockitem {
  border: 2px solid #333; /* Darker border for better visibility */
  border-radius: 8px; /* Rounded corners for a softer look */
  background-color: #fff; /* White background for each item */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* Subtle shadow for depth */
  overflow: hidden; /* Ensure content does not overflow the border */
  width: 250px; /* Fixed width for consistency */
  text-align: center; /* Center text within each item */
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.stockitem:hover {
  transform: scale(1.05); /* Slightly enlarge the item on hover */
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3); /* Enhance shadow on hover */
}

.stock-image {
  width: 100%; /* Ensure image fills the container width */
  height: auto; /* Maintain aspect ratio */
  border-bottom: 2px solid #333; /* Border to separate image from text */
}

.crypto-container {
